.SecondaryNav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    padding: 0 3.3%;
    box-sizing: border-box;
    position: fixed;
    top: 60px;
    transform: translateY(-100%);
    transition: transform 200ms ease-in;
    background: white;
    z-index: 998;
}

.SecondaryNav[data-show="true"] {
    transform: translateY(0%);
}

.SecondaryNavLinkContainer {
    display: flex;
    flex-direction: column;
}

.SecondaryNavLink {
    font-size: 50px;
    color: black;
    text-decoration: none;
}

.SecondaryNavLink:before {
    content: ">"
}