.PrimaryNav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    padding: 0 3.3%;
    box-sizing: border-box;
    position: fixed;
    height: 60px;
    top: 0;
    transition: top 200ms ease-in;
    background: white;
    z-index: 999;
}

.PrimaryNavLink {
    font-size: 50px;
    color: black;
    text-decoration: none;
}